import React, { ReactElement, useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router";
import Cytoscape from "cytoscape";
//import COSEBilkent from "cytoscape-cose-bilkent";
import CytoscapeComponent from "react-cytoscapejs";

//Cytoscape.use(COSEBilkent);

export const CytoChart = (props: any): ReactElement => {
  const cyRef = useRef<Cytoscape.Core>();
  let navigate = useNavigate();

  const [elements, setElements] = useState(props.elements);

  // This effect runs once on mount and handles setting up imperative
  // event listeners on the Cytoscape core ref
  useEffect(() => {
    const cy = cyRef.current;
    if (!cy) {
      return;
    }
    cy.on("select", "node", (event) => {
      const data = event.target[0].data();
      if(data.route) {
        console.log(data.route);
        
        navigate(data.route);
      }
      
    });
  }, []);

//   useEffect(() => {
//     setTimeout(() => {
//       setElements([
//         ...elements,
//         { data: { id: "Closed", label: "Closed" }, selectable: true },
//         {
//           data: {
//             source: "Approved",
//             target: "Closed",
//             label: "close",
//             id: "a7",
//           },
//         },
//       ]);
//     }, 3000);
//   }, []);

  const layout = {
    name: "preset",
    padding: 5,
    nodeDimensionsIncludeLabels: true,
  };

  const cytoscapeStylesheet: Array<cytoscape.Stylesheet> = [
    {
      selector: "node",
      css: {
        'shape': 'rectangle',
        'content': 'data(label)',
        'text-valign': 'center',
        'text-halign': 'center'
      }
    },
    {
      selector: ':parent',
      css: {
        'text-valign': 'top',
        'text-halign': 'center',
        'shape': 'round-rectangle',
        'corner-radius': "10",
        'padding': 10
      }
    },
    {
      selector: 'node#e',
      css: {
        'corner-radius': "10",
        'padding': 0
      }
    },
    {
      selector: 'edge',
      css: {
        'curve-style': 'straight',
        'target-arrow-shape': 'none'
      }
    }
  ];

  return (
    <div style={{position: "relative", width: "40dvw", height: "100%", maxWidth: '1000px'}}>
    <CytoscapeComponent
      boxSelectionEnabled={true}
       cy={(cy): void => {
         cyRef.current = cy;
       }}
      elements={elements}
      layout={layout}
      style={{
        top: 0,
        //bottom: 0,
        position: "absolute",
        //width: "100%",
        width: "100%",
        height: "100%"
      }}
      stylesheet={cytoscapeStylesheet}
    />
    </div>
  );
};
