import { Panel, PanelGroup, PanelResizeHandle } from "react-resizable-panels";
import { CytoChart } from "../CytoChart/CytoChart.tsx"
import styles from "./styles.module.css"
import elements from "../../data/cytoElements.ts";
import { Routes, Route } from "react-router";

function MainLayout() {
  return (
    <>
    <PanelGroup direction="horizontal" style={{minHeight: "100dvh"}}>
      <Panel className={styles.panel} defaultSize={40} minSize={10} maxSize={40}>
        <CytoChart elements={elements}/>
      </Panel>
      <PanelResizeHandle className={styles.resizeHandle} />
      <Panel className={styles.panel} minSize={10}>
        <Routes>
        <Route path="/" element={<>Root</>} />
        <Route path="foo" element={<>FOO</>} />
        <Route path="bar" element={<>BAR</>} />
        <Route path="*" element={<>404</>} />
      </Routes>
      </Panel>
    </PanelGroup>
    </>
  );
}

export default MainLayout;
